// Rem output with px fallback
@mixin font-size($sizeValue: 1) {
	font-size: ($sizeValue * 16) * 1px;
	font-size: $sizeValue * 1rem;
}

// Center block
@mixin center-block {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

@mixin push-auto {
	margin: { 
		left: auto;
		right: auto;
	}
  }
  
@mixin content-wrapper{
	width: 1120px;
    margin-right: auto;
	margin-left: auto;
}